import { BrowserModule } from '@angular/platform-browser';
import { NgModule } from '@angular/core';
import { FormsModule } from '@angular/forms';

import { AppComponent } from './app.component';

import { MsalModule, MsalInterceptor, MsalAngularConfiguration, MSAL_CONFIG, MSAL_CONFIG_ANGULAR, MsalService } from '@azure/msal-angular';
import { UsersComponent } from './users/users.component';
import { HTTP_INTERCEPTORS, HttpClientModule } from '@angular/common/http';
import { AppRoutingModule } from './app-routing.module';
import { HelpComponent } from './help/help.component';
import { Configuration } from 'msal';
import { UserprofileComponent } from './userprofile/userprofile.component';
import { AuditlogsComponent } from './auditlogs/auditlogs.component';

import { environment } from '../environments/environment';
import { SecurescoreComponent } from './securescore/securescore.component';
import { MessagesComponent } from './messages/messages.component';
import { TenantcheckComponent } from './tenantcheck/tenantcheck.component';
import { UserextattrsComponent } from './userextattrs/userextattrs.component';

const isIE = window.navigator.userAgent.indexOf("MSIE ") > -1 || window.navigator.userAgent.indexOf("Trident/") > -1;
function MSALConfigFactory(): Configuration {
  return {
    auth: {
      clientId: environment.clientId
      , authority: environment.authority
      , redirectUri: environment.redirectUri
    },
    cache: {
      cacheLocation: "localStorage",
      storeAuthStateInCookie: true
    }
  };
}

function MSALAngularConfigFactory(): MsalAngularConfiguration {
  return {
    consentScopes: [
      "user.read.all",
      //"user.read",
      //"directory.read.all"
      //, "SecurityEvents.Read.All"
      //, "SecurityEvents.ReadWrite.All"
    ],
    protectedResourceMap: [
      [environment.graphPrefix + 'v1.0/users', ['user.read.all']],
      [environment.graphPrefix + 'v1.0/me', ['user.read']],
      ['https://graph.microsoft.us/v1.0/users', ['user.read.all']],
      ['https://graph.microsoft.us/v1.0/users', ['user.read']]
      //, [environment.graphPrefix + 'v1.0/auditLogs/signIns', ['auditlog.read.all']]
      //, [environment.graphPrefix + 'beta/security/secureScores', ['SecurityEvents.Read.All', 'SecurityEvents.ReadWrite.All']]
    ],
  };
}

@NgModule({
  declarations: [
    AppComponent,
    UsersComponent,
    HelpComponent,
    UserprofileComponent,
    AuditlogsComponent,
    SecurescoreComponent,
    MessagesComponent,
    TenantcheckComponent,
    UserextattrsComponent
  ],
  imports: [
    BrowserModule,
    FormsModule,
    AppRoutingModule,
    HttpClientModule,
    MsalModule
  ],
  providers: [
    {
      provide: HTTP_INTERCEPTORS,
      useClass: MsalInterceptor,
      multi: true
    },
    {
      provide: MSAL_CONFIG,
      useFactory: MSALConfigFactory
    },
    {
      provide: MSAL_CONFIG_ANGULAR,
      useFactory: MSALAngularConfigFactory
    },
    MsalService
  ],
  bootstrap: [AppComponent]
})
export class AppModule { }
