import { Injectable } from '@angular/core';

@Injectable({
  providedIn: 'root'
})
export class MessagesService {

  messages: string[] = [];
  error: any = null;

  constructor() { }

  add(message: string){
    this.messages.push(message);
    this.error = null;
  }

  clear() {
    this.messages = [];
    this.error = null;
  }

  addError(error: any) {
    this.error = error;
  }
}
