import { Component, OnInit } from '@angular/core';
import { TenantcheckService } from './tenantcheck.service';
import { MessagesService } from '../messages/messages.service';
import { environment } from '../../environments/environment'

@Component({
  selector: 'app-tenantcheck',
  templateUrl: './tenantcheck.component.html',
  styleUrls: ['./tenantcheck.component.css']
})
export class TenantcheckComponent implements OnInit {

  public loading = false;
  public showResult = false;
  public production = environment.production;

  public error = "";
  public message = "";
  public tenantLocation = "";
  public tenantWhiteListed = "";
  public tenantRegion = "";
  public tenantId = "";
  public result = "Results will show here...";
  public tenantB2BAvailable = "";
  public tenantName = "";

  constructor(private tenantService: TenantcheckService, private messageService: MessagesService) { }

  ngOnInit(): void {
    this.messageService.clear();
  }

  toggleResult() {
    this.showResult = !this.showResult;
  }

  resetView() {
    this.showResult = false;
    this.loading = true;

    this.error = "";
    this.tenantLocation = "";
    this.tenantWhiteListed = "";
    this.tenantRegion = "";
    this.tenantId = "";
    this.result = "Results will show here...";
    this.tenantB2BAvailable = "";
  }

  checkTenant() {
    this.resetView();
    this.message = "Checking Tenant...";
    this.loading = true;

    console.log("Tenant Name: " + this.tenantName);

    this.tenantService.checkTenant(this.tenantName)
      .then(result => {
        console.log("Result: " + JSON.stringify(result));
        this.result = JSON.stringify(result);
        this.getTenantLocation(result.tenant_region_scope);
        this.getB2BAvailability(result.tenant_region_scope);
        this.isWhiteListed(result.cloud_instance_name, result.tenant_regionScope);
        this.getTenantRegion(result.tenant_region_scope, result.tenant_region_sub_scope);
        this.getTenantId(result.token_endpoint);

        this.loading = false;
        this.message = "";
      })
      .catch(error => {
        console.error("Error: " + JSON.stringify(error));
        this.error = error.error.error;
        this.result = JSON.stringify(error.error);

        if(error.error != null && error.error.error == "invalid_tenant") {
          this.loading = false;
          this.message = "";
        }
        else {
          this.loading = false;
          this.message = error.error.error_description;
        }
      });
    return false;
  }

  getTenantId(token_endpoint: string) {
    this.tenantId = token_endpoint.replace("https://login.microsoftonline.us/", "")
    .replace("/oauth2/token", "")
    .replace("https://login.microsoftonline.com/", "");
  }

  getTenantLocation(tenant_region_scope: string) {
    if(tenant_region_scope == "USG") {
      this.tenantLocation = "Fairfax";
    }
    else if(tenant_region_scope == "USGov") {
      this.tenantLocation =  "Arlington";
    }
    else {
      this.tenantLocation = "Commercial - " + tenant_region_scope;
    }
  }

  getB2BAvailability(tenant_region_scope: string) {
    if(tenant_region_scope == "USG") {
      this.tenantB2BAvailable = "Not Available";
    }
    else if(tenant_region_scope == "USGov") {
      this.tenantB2BAvailable = "Available";
    }
    else {
      this.tenantB2BAvailable = "Available";
    }
  }

  isWhiteListed(cloud_instance_name: string, tenant_region_scope: string) {
    if(tenant_region_scope == "USG" && cloud_instance_name == "microsoftonline.com") {
      this.tenantWhiteListed = "No";
    }
    else if(tenant_region_scope == "USGov" && cloud_instance_name == "microsoftonline.com") {
      this.tenantWhiteListed = "No - Unexpected!";
    }
    else {
      this.tenantWhiteListed = "Yes";
    }
  }

  getTenantRegion(tenant_region_scope: string, tenant_region_sub_scope: string) {
    if(tenant_region_sub_scope == "DODCON") {
      this.tenantRegion = "GCCH (IL4)";
    }
    else if(tenant_region_sub_scope == "DOD") {
      this.tenantRegion = "DOD (IL5)";
    }
    else if(tenant_region_sub_scope == "GCC") {
      this.tenantRegion = "GCC (IL2)";
    }
    else if(tenant_region_scope == "USG" || tenant_region_scope == "USGov") {
      this.tenantRegion = "Azure Gov Only";
    }
    else {
      this.tenantRegion = "Worldwide";
    }
  }
}
