import { Component, OnInit } from '@angular/core';
import { UserextattrsService } from './userextattrs.service';
import { MessagesService } from '../messages/messages.service';
import { environment } from '../../environments/environment';

@Component({
  selector: 'app-userextattrs',
  templateUrl: './userextattrs.component.html',
  styleUrls: ['./userextattrs.component.css']
})
export class UserextattrsComponent implements OnInit {

  public users: [];
  public selectString: string = "$select=userPrincipalName,displayName,jobTitle,mail";
  public includeAttributes = [
    "onPremisesDistinguishedName", 
    "onPremisesDomainName", 
    "onPremisesExtensionAttributes", 
    "onPremisesImmutableId",
    "onPremisesLastSyncDateTime",
    "onPremisesProvisioningErrors",
    "onPremisesSamAccountName",
    "onPremisesSecurityIdentifier",
    "onPremisesSyncEnabled",
    "onPremisesUserPrincipalName"
  ];

  constructor(private extService: UserextattrsService, private messageService: MessagesService) { }

  public loading = false;
  public govTenant = false;
  public production = environment.production;

  ngOnInit(): void {
    this.messageService.clear();

    this.getUsers();
  }

  getUsers() {
    this.users = [];
    this.loading = true;
    this.extService.getUsers(this.selectString, this.govTenant)
      .then(result => {
        this.messageService.add("getUsers returned success");
        this.users = result.value;
        this.loading = false;
      })
      .catch(error => {
        console.error("Error: " + JSON.stringify(error));
        this.messageService.addError(error);
        this.loading = false;
      }
    );
  }

  onChange(cbValue: string, cbChecked: boolean) {
    var appendValue = "," + cbValue
    if(cbChecked) {
      this.selectString += appendValue;
    }
    else {
      this.selectString = this.selectString.replace(appendValue, "");
    }
    this.messageService.add("Value: " + cbValue + " checked: " + cbChecked + ". SelectString: " + this.selectString);
    console.log("Value: " + cbValue + " checked: " + cbChecked + " SELECT STRING: " + this.selectString);
  }

  onTenantChange(isGovTenant: boolean) {
    this.govTenant = isGovTenant;
  }
}
