import { Component, OnInit } from '@angular/core';
import { SecurescoreService } from './securescore.service';
import { MessagesService } from '../messages/messages.service';
import { MsalService } from '@azure/msal-angular';

@Component({
  selector: 'app-securescore',
  templateUrl: './securescore.component.html',
  styleUrls: ['./securescore.component.css']
})
export class SecurescoreComponent implements OnInit {
  public secureScores: any;

  constructor(private securescoreservice: SecurescoreService, 
    private messageService: MessagesService,
    private msalService: MsalService) { }

  ngOnInit(): void {
     this.securescoreservice.getSecureScores()
     .then(result => {
       console.log("Result: " + JSON.stringify(result));
       this.secureScores = result;
     })
     .catch(error => {
       console.log("Error: " + JSON.stringify(error));
       this.messageService.addError(error);
     });
  }
}
