import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { UsersComponent } from './users/users.component';
import { HelpComponent } from './help/help.component';
import { MsalGuard } from '@azure/msal-angular';
import { UserprofileComponent } from './userprofile/userprofile.component';
import { SecurescoreComponent } from './securescore/securescore.component';
import { TenantcheckComponent } from './tenantcheck/tenantcheck.component';
import { UserextattrsComponent } from './userextattrs/userextattrs.component';


const routes: Routes = [
  {path: "", redirectTo: "/tenantcheck", pathMatch: "full" },
  {path: "tenantcheck", component: TenantcheckComponent },
  {path: "userextattrs", component: UserextattrsComponent, canActivate: [MsalGuard]},
  {path: "users", component: UsersComponent, canActivate: [MsalGuard]},
  {path: "securescore", component: SecurescoreComponent, canActivate: [MsalGuard]},
  {path: "userprofile", component: UserprofileComponent, canActivate: [MsalGuard]},
  {path: "help", component: HelpComponent}
];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class AppRoutingModule { }
